











































export default {
	data: {
		telnumber: '',
		studentid: '',
		password: '',
		username: '',
		code: '',
		button: '获取验证码',
	},
	methods: {
		//用于注册
		getRegisterIfm(telnumber, studentid, username, password, code) {
			if ((code == '' || code == "0519") && this.button.charAt(this.button.length - 1) == '码') {
				uni.showToast({
					icon: 'none',
					title: '验证码是0519'
				});
				this.button = '注册';
				return;
			}
			if (verification(telnumber, studentid, username, password, code) == 0) {
				return;
			}
			if (code != '0519' && this.button.charAt(this.button.length - 1) == '册') {
				uni.showToast({
					icon: 'none',
					title: '请重新获取验证码'
				});
				this.button = '获取验证码';
				return;
			}
			uni.showLoading({
				title: '请稍后...',
				mask: 'true'
			});
			uni.request({
				url: 'http://127.0.0.1:8080/user/register',
				// url: 'http://192.168.191.1:8080/user/register',
				method: 'POST',
				data: {
					telnumber: telnumber,
					studentid: studentid,
					username: username,
					password: password,
				},
				success: (res) => {
					console.log(res.data);
					if (res.data == 'Success') {
						uni.setStorageSync('telnumber', telnumber);
						uni.setStorageSync('password', password);
						uni.hideLoading();
						uni.reLaunch({
							url: '../login/login'
						});
					} else {
						if (res.data.length < 20) {
							uni.hideLoading();
							uni.showToast({
								icon: 'none',
								title: res.data
							});
						}
						if (res.data.length > 20) {
							uni.hideLoading();
							uni.showToast({
								icon: 'none',
								title: '404！请联系管理员'
							});
						}
					}
				}
			});
		}

	}
}

function verification(telnumber, studentid, username, password, code) {
	var teltest = /^(((13[0-9]{1})|(15[0-9]{1})|(18[0-9]{1}))+\d{8})$/;
	var idtest = /^[0-9]+$/;
	var nametest = /^[\u0391-\uFFE5]+$/;
	var pwdtest = /^[0-9a-zA-Z]*$/;
	if (telnumber.length == 0 || telnumber.length != 11 || !teltest.test(telnumber)) {
		uni.showToast({
			icon: 'none',
			title: '请输入有效的手机号'
		});
		return 0;
	}
	if (studentid.length == 0 || !idtest.test(studentid)) {
		uni.showToast({
			icon: 'none',
			title: '请输入有效的学号'
		});
		return 0;
	}
	if (username.length == 0 || !nametest.test(username)) {
		uni.showToast({
			icon: 'none',
			title: '请输入中文名'
		});
		return 0;
	}
	if (password.length == 0 || !pwdtest.test(password) || password.length < 6) {
		uni.showToast({
			icon: 'none',
			title: '密码6位以上的字母加数字'
		});
		return 0;
	}
	return 1;

}
